import { httpSnackbar, httpSilent } from '@/plugins/axios'
import CreciHelper from '@/helpers/CreciHelper';
import StringHelper from '@/helpers/StringHelper';
import {loadImageAsync} from "@/plugins/loadImage";
import ImageHelper from "@/helpers/ImageHelper";

export default class I2brWebClient {
  static regionais = [0, 5, 6, 8, 9, 11, 12, 14, 15, 16, 18, 20, 21, 22, 23, 24, 26];

  /**
   * Informa se o regional utiliza o sistema ConselhoNET.
   * @param {number} regionalId
   * @returns {boolean}
   */
  static utilizaConselhoNet(regionalId) {
    return this.regionais.includes(regionalId);
  }

  #http = httpSnackbar();
  #cpf;
  #idregional;

  /**
   * Constructor
   * @param {string} cpf
   * @param {number} idregional
   * @param {boolean} silent
   */
  constructor(cpf, idregional, silent = false) {
    this.#cpf = StringHelper.extractNumbers(cpf);
    this.#idregional = idregional;
    if (silent) this.#http = httpSilent();
    this.#http.defaults.baseURL = 'https://api.conselho.net.br/';
  }

  /**
   * Obtem os metadados da ficha cadastral do corretor.
   * @returns {Promise<any>}
   */
  async getFicha() {
    const formData = new FormData();
    formData.append('cpf', this.#cpf);
    formData.append('idregional', this.#idregional.toString());
    const { data: ficha } = await this.#http.post('corretor/ficha.php', formData);
    return ficha;
  }

  /**
   * Obtem a foto do corretor em Base64 ou Blob
   * @param {boolean} blob Retorna a foto em Blob
   * @returns {Promise<boolean|Blob|string>} Imagem em string base64 ou Blob. False em caso de falha.
   */
  async getFoto(blob) {
    const formData = new FormData();
    formData.append('cpf', this.#cpf);
    formData.append('idregional', this.#idregional.toString());
    const response = await this.#http.post('corretor/obter/foto.php', formData, {responseType: 'blob', validateStatus: status => status===200 || status===404});
    if (response.status !== 200) return false;
    const canvas = await loadImageAsync(response.data, {
      maxWidth: 300,
      maxHeight: 400,
      minWidth: 300,
      minHeight: 400,
      canvas: true,
      crop: true,
      orientation: true,
    });
    const base64 = canvas.toDataURL('image/jpeg', .96);
    return blob ? ImageHelper.dataURItoBlob(base64) : base64;
  }

  /**
   * Obtem a assinatura do corretor em Base64 ou Blob
   * @param {boolean} blob Retorna a foto em Blob
   * @returns {Promise<boolean|Blob|string>} Imagem em string base64 ou Blob. False em caso de falha.
   */
  async getAssinatura(blob) {
    const formData = new FormData();
    formData.append('cpf', this.#cpf);
    formData.append('idregional', this.#idregional.toString());
    const response = await this.#http.post('corretor/obter/assinatura.php', formData, {responseType: 'blob', validateStatus: status => status===200 || status===404});
    if (response.status !== 200) return false;
    const canvas = await loadImageAsync(response.data, {
      maxWidth: 1920,
      maxHeight: 1080,
      canvas: true,
      orientation: true,
    });
    const base64 = canvas.toDataURL('image/png');
    return blob ? ImageHelper.dataURItoBlob(base64) : base64;
  }

  /**
   * Obtem metadados sobre a regularidade do corretor.
   * @returns {Promise<any>}
   */
  async getCertidaoInfo() {
    //TODO - No momento não funciona por falta do header Access-Control-Allow-Origin na resposta.
    const {data} = await this.#http.get('https://www.creci' + CreciHelper.idParaUf(this.#idregional) + '.conselho.net.br/api_validade_carp_icorretor.php?CPF_CNPJ=' + this.#cpf);
    return data;
  }
}