export default class CreciHelper {
  static regionais = [
    //{id: 0, uf: "XX", ufExtenso: "Teste"},
    {id: 1, uf: "RJ", ufExtenso: "Rio de Janeiro"},
    {id: 2, uf: "SP", ufExtenso: "São Paulo"},
    {id: 3, uf: "RS", ufExtenso: "Rio Grande do Sul"},
    {id: 4, uf: "MG", ufExtenso: "Minas Gerais"},
    {id: 5, uf: "GO", ufExtenso: "Goiás"},
    {id: 6, uf: "PR", ufExtenso: "Paraná"},
    {id: 7, uf: "PE", ufExtenso: "Pernambuco"},
    {id: 8, uf: "DF", ufExtenso: "Distrito Federal"},
    {id: 9, uf: "BA", ufExtenso: "Bahia"},
    {id: 11, uf: "SC", ufExtenso: "Santa Catarina"},
    {id: 12, uf: "PA", ufExtenso: "Pará"},
    {id: 13, uf: "ES", ufExtenso: "Espírito Santo"},
    {id: 14, uf: "MS", ufExtenso: "Mato Grosso do Sul"},
    {id: 15, uf: "CE", ufExtenso: "Ceará"},
    {id: 16, uf: "SE", ufExtenso: "Sergipe"},
    {id: 17, uf: "RN", ufExtenso: "Rio Grande do Norte"},
    {id: 18, uf: "AM-RR", ufExtenso: "Amazonas & Roraima"},
    {id: 19, uf: "MT", ufExtenso: "Mato Grosso"},
    {id: 20, uf: "MA", ufExtenso: "Maranhão"},
    {id: 21, uf: "PB", ufExtenso: "Paraíba"},
    {id: 22, uf: "AL", ufExtenso: "Alagoas"},
    {id: 23, uf: "PI", ufExtenso: "Piauí"},
    {id: 24, uf: "RO", ufExtenso: "Rondônia"},
    {id: 25, uf: "TO", ufExtenso: "Tocantins"},
    {id: 26, uf: "AC", ufExtenso: "Acre"}
  ];

  /**
   * Obtem a sigla do estado ou o nome por extenso através do numero do regional.
   * @param {number} regionalId Numero do regional.
   * @param {boolean} extenso Retornar nome por extenso.
   * @param {boolean} upper Retorno do nome extenso em caixa alta.
   * @returns {boolean|string} False em caso de regional desconhecido.
   */
  static idParaUf(regionalId, extenso = false, upper = false) {
    if (typeof regionalId === 'string') regionalId = parseInt(regionalId);
    const regional = this.regionais.find(i => i.id === regionalId);
    if (!regional) return false;
    const uf = extenso ? regional.ufExtenso : regional.uf;
    return upper ? uf.toUpperCase() : uf;
  }

  /**
   * Obtem o numero do regional através da sigla do estado.
   * @param {string} regionalUf UF do regional em duas letras.
   * @returns {boolean|number} False em caso de regional desconhecido.
   */
  static ufParaId(regionalUf) {
    if (typeof regionalUf !== 'string' || regionalUf.length !== 2) return false;
    regionalUf = regionalUf.toUpperCase();
    const regional = this.regionais.find(i => i.uf === regionalUf);
    return regional ? regional.id : false;
  }
}