import Vue from 'vue';
import store from '@/store';
import {Alert, Snackbar} from '@/models';

/**
 * Exibe um alerta
 * @param {string} text
 * @param {'success'|'info'|'warning'|'error'} type
 * @param {'top'|'right'|'bottom'|'left'} border
 * @param {boolean} coloredBorder
 * @param {boolean} dense
 * @param {boolean} dismissible
 * @param {number} timeout
 */
Vue.prototype.$alert = ({text, type = 'info', border = undefined, coloredBorder = false, dense = false, dismissible = false, timeout = 3000}) => {
    store.commit('alert/show', new Alert({text, type, border, coloredBorder, dense, dismissible, timeout}));
};

/**
 * Exibe um loader
 * @param {boolean} active
 */
Vue.prototype.$loading = (active) => {
    if (active) store.commit('setLoader', true);
    else store.commit('setLoader', false);
};

/**
 * Exibe o snackbar global da aplicação
 * @param {string} text
 * @param {string} color
 * @param {boolean} top
 * @param {boolean} bottom
 * @param {boolean} left
 * @param {boolean} right
 * @param {number} timeout
 * @param {boolean} vertical
 * @param {boolean} multiLine
 * @param {boolean} absolute
 */
Vue.prototype.$snackbar = ({text, color = 'primary', top = false, bottom = false, left = false, right = false, timeout = 5000, vertical = false, multiLine = false, absolute = false}) => {
    store.commit('snackbar/show', new Snackbar({
        text,
        color,
        top,
        bottom,
        left,
        right,
        timeout,
        vertical,
        multiLine,
        absolute,
    }));
};