import AppWebClient from '@/http/AppWebClient';
import ImageHelper from '@/helpers/ImageHelper';
import moment from 'moment';

export default {
  namespaced: true,
  state: () => ({
    cadastro: null, //Ficha global
    cadastroLoading: false,
    cadastroData: null, //Datetime do ultimo carregamento dos dados
    foto: null, //Base64
    assinatura: null, //Base64
  }),
  mutations: {
    setCadastro(state, cadastro) {
      state.cadastro = cadastro;
      state.cadastroData = moment().format('YYYY-MM-DD HH:mm:ss');
      state.cadastroLoading = false;
    },
    setCadastroLoading(state, payload) {
      state.cadastroLoading = !!payload;
    },
    setFoto(state, foto) {
      state.foto = foto;
    },
    setAssinatura(state, assinatura) {
      state.assinatura = assinatura;
    },
    destroy(state) {
      state.cadastro = null;
      state.foto = null;
      state.assinatura = null;
    },
  },
  actions: {
    async reloadCadastro({commit}) {
      commit('setCadastroLoading', true);
      const appWebClient = new AppWebClient();
      const ficha = await appWebClient.getFicha();
      commit('setCadastro', ficha);
    },
    async reloadFoto({commit}) {
      const appWebClient = new AppWebClient();
      let foto = await appWebClient.getFoto();
      if (foto) {
        foto = await ImageHelper.blobToDataURI_async(foto);
        commit('setFoto', foto);
      } else {
        commit('setFoto', null);
      }
    },
    async reloadAssinatura({commit}) {
      const appWebClient = new AppWebClient();
      let assinatura = await appWebClient.getAssinatura();
      if (assinatura) {
        assinatura = await ImageHelper.blobToDataURI_async(assinatura);
        commit('setAssinatura', assinatura);
      } else {
        commit('setAssinatura', null);
      }
    },
    async reloadAll({dispatch}) {
      await Promise.all([
        dispatch('reloadCadastro'),
        dispatch('reloadFoto'),
        dispatch('reloadAssinatura'),
      ]);
    },
  },
  getters: {
    avatar(state) {
      return state.foto ? state.foto : 'img/avatar.svg';
    },
  },
}