import { httpSnackbar, httpSilent } from '@/plugins/axios'
import JsonPackage from '../../package.json'

export default class SessionWebClient {
  #http = httpSnackbar();

  /**
   * Construtor
   * @param {boolean} silent
   */
  constructor(silent = false) {
    if (silent) this.#http = httpSilent();
  }

  /**
   * Cria uma nova sessão de login, retornando a string JSON WEB TOKEN
   * @param {string} regional Numero da regiao.
   * @param {string} cpf CPF do usuario.
   * @param {string|null} password Senha da conta. Se já estiver logado não precisa passar (alternancia de conta).
   * @returns {Promise<string>} JSON WEB TOKEN.
   */
  async createSession(regional, cpf, password = null) {
    const formData = new FormData();
    formData.append('regional', regional);
    formData.append('cpf', cpf);
    formData.append('versao', JsonPackage.version);
    if (password) formData.append('password', password);
    const {data} = await this.#http.post('login', formData);
    return data;
  }

  /**
   * Valida a sessão atual do usuário
   * @param {boolean} onlyLast Considera válido apenas se a sessão é a última.
   * @param {boolean} newToken Após a validação, uma nova sessão será registrada e seu token será retornado.
   * @returns {Promise<string|void>}
   */
  async validateSession(onlyLast = true, newToken = false) {
    const formData = new FormData();
    formData.append('versao', JsonPackage.version);
    if (!onlyLast) formData.append('all', 'S');
    if (newToken) formData.append('newtoken', 'S');
    const {data} = await this.#http.post('session', formData);
    return data;
  }
}