<template>
  <v-container
    fluid
    class="fill-height"
    style="background: linear-gradient(90deg, #0072ff 0%, #00d4ff 100%);"
  >
    <!-- Formulario de login -->
    <v-card class="mx-auto" elevation="5" shaped width="400" max-width="94%">
      <v-card-title class="flex-column pb-0">
        <v-avatar class="elevation-3 mb-3" height="6rem" width="6rem">
          <img src="@/assets/logo.svg">
        </v-avatar>
        <p class="title mb-2">iCorretor</p>
      </v-card-title>
      <v-card-text>
        <!-- Alerta -->
        <v-alert
          v-for="i in $store.state.alertas.filter(i => i.local === 1)"
          :key="i.id"
          :type="i.tipo"
          class="mb-2"
          dense
        >
          {{i.texto}}
        </v-alert>
        <v-form @submit.prevent="submitLogin" ref="form-login" :disabled="disableForms">
          <v-select
            label="Regional"
            prepend-inner-icon="mdi-map-marker-radius"
            :items="regionais"
            item-value="id"
            item-text="ufExtenso"
            :rules="rules.regionalRules"
            class="mb-2"
            v-model="iptRegional"
            :messages="compatibilidadeParcial ? 'Este regional não tem compatibilidade total' : undefined"
          >
            <template v-slot:selection="{item}">{{item.id}}º - {{item.ufExtenso.toUpperCase()}}</template>
            <template v-slot:item="{item}">{{item.id}}º - {{item.ufExtenso.toUpperCase()}}</template>
          </v-select>
          <v-text-field
            label="CPF"
            placeholder="cpf"
            prepend-inner-icon="mdi-account"
            :rules="rules.cpfRules"
            type="tel"
            autocomplete="username"
            v-mask="'###.###.###-##'"
            class="mb-2"
            v-model="iptCpf"
          ></v-text-field>
          <v-text-field
            label="Senha"
            placeholder="senha"
            prepend-inner-icon="mdi-lock"
            :rules="rules.passwordRules"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            :type="showPassword ? 'text' : 'password'"
            @click:append="showPassword = !showPassword"
            autocomplete="current-password"
            autocapitalize="off"
            v-model="iptPassword"
          ></v-text-field>
          <v-btn
            rounded
            color="primary"
            type="submit"
            block
            :loading="loadingLogin"
          >ENTRAR</v-btn>
          <v-btn
            rounded
            outlined
            type="button"
            block
            class="mt-3"
            @click="dialogRecover = true"
            :disabled="loadingLogin"
          >Esqueci minha senha</v-btn>
          <v-btn
            text
            small
            outlined
            type="button"
            class="mx-auto d-block mt-3"
            @click="dialogFirstAccess = true"
            :disabled="loadingLogin"
          >Primeiro acesso</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
    <!-- Formulario de recuperacao de senha -->
    <v-dialog v-model="dialogRecover" width="400" max-width="94%" :persistent="loadingRecover">
      <v-card>
        <v-form @submit.prevent="submitRecover" ref="form-recover" :disabled="disableForms">
          <v-card-title>Recuperar senha</v-card-title>
          <v-card-subtitle>Enviaremos uma nova senha para o seu e-mail</v-card-subtitle>
          <v-card-text>
            <v-select
                label="Regional"
                prepend-inner-icon="mdi-map-marker-radius"
                :items="regionais"
                item-value="id"
                item-text="ufExtenso"
                :rules="rules.regionalRules"
                class="mb-2"
                v-model="iptRegional"
            >
              <template v-slot:selection="{item}">{{item.id}}º - {{item.ufExtenso.toUpperCase()}}</template>
              <template v-slot:item="{item}">{{item.id}}º - {{item.ufExtenso.toUpperCase()}}</template>
            </v-select>
            <v-text-field
                label="CPF"
                placeholder="cpf"
                prepend-inner-icon="mdi-account"
                :rules="rules.cpfRules"
                type="tel"
                autocomplete="username"
                v-mask="'###.###.###-##'"
                class="mb-2"
                v-model="iptCpf"
            ></v-text-field>
            <v-text-field
                label="E-mail"
                placeholder="e-mail"
                prepend-inner-icon="mdi-email"
                :rules="rules.emailRules"
                type="email"
                class="mb-2"
                v-model="iptEmail"
            ></v-text-field>
          </v-card-text>
          <v-card-actions class="justify-center">
            <v-btn color="primary" type="submit" :loading="loadingRecover">Prosseguir</v-btn>
            <v-btn color="secondary" @click="dialogRecover = false" :disabled="loadingRecover">Cancelar</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <!-- Formulario de primeiro acesso -->
    <v-dialog v-model="dialogFirstAccess" width="400" max-width="94%" :persistent="loadingFirstAccess">
      <v-card>
        <v-form @submit.prevent="submitFirstAccess" ref="form-firstaccess" :disabled="disableForms">
          <v-card-title>Primeiro acesso</v-card-title>
          <v-card-subtitle>Cadastrando conta de acesso</v-card-subtitle>
          <v-card-text>
            <v-select
                label="Regional"
                prepend-inner-icon="mdi-map-marker-radius"
                :items="regionais"
                item-value="id"
                item-text="ufExtenso"
                :rules="rules.regionalRules"
                class="mb-2"
                v-model="iptRegional"
            >
              <template v-slot:selection="{item}">{{item.id}}º - {{item.ufExtenso.toUpperCase()}}</template>
              <template v-slot:item="{item}">{{item.id}}º - {{item.ufExtenso.toUpperCase()}}</template>
            </v-select>
            <v-text-field
                label="CPF"
                placeholder="cpf"
                prepend-inner-icon="mdi-account"
                :rules="rules.cpfRules"
                type="tel"
                autocomplete="username"
                v-mask="'###.###.###-##'"
                class="mb-2"
                v-model="iptCpf"
            ></v-text-field>
            <v-text-field
                label="E-mail"
                placeholder="e-mail"
                prepend-inner-icon="mdi-email"
                :rules="rules.emailRules"
                type="email"
                class="mb-2"
                v-model="iptEmail"
            ></v-text-field>
            <v-text-field
                label="Nova senha"
                placeholder="senha"
                prepend-inner-icon="mdi-lock"
                :rules="rules.passwordRules"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :type="showPassword ? 'text' : 'password'"
                @click:append="showPassword = !showPassword"
                autocapitalize="off"
                v-model="iptPassword"
                hint="min. 6 digitos"
            ></v-text-field>
            <v-text-field
                label="Repita a senha"
                placeholder="senha"
                prepend-inner-icon="mdi-lock"
                :rules="[...rules.passwordRules, rulePasswordRepeat]"
                type="password"
                autocapitalize="off"
                v-model="iptPasswordRepeat"
            ></v-text-field>
          </v-card-text>
          <v-card-actions class="justify-center">
            <v-btn color="primary" type="submit" :loading="loadingFirstAccess">Cadastrar</v-btn>
            <v-btn color="secondary" @click="dialogFirstAccess = false" :disabled="loadingFirstAccess">Cancelar</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <!-- Instrucao para ativar a conta -->
    <v-dialog v-model="dialogEmailAtivacao" width="400" max-width="94%">
      <v-card>
        <v-card-title>
          Conta não confirmada
          <v-spacer></v-spacer>
          <v-icon @click="dialogEmailAtivacao = false">mdi-close</v-icon>
        </v-card-title>
        <v-card-text>
          Por questões de segurança, enviamos uma mensagem para o seu e-mail (<strong>{{emailAtivacao}}</strong>) com a instrução para ativar sua conta.<br/>
          Este procedimento garante que a conta tenha sido criada pelo corretor, e que o mesmo possui acesso ao e-mail informado.
          <v-btn color="primary" block rounded class="my-2" @click="dialogEmailAtivacao = false" :disabled="loadingReenviarEmail">OK</v-btn>
          <v-btn color="warning" block rounded @click="reenviarEmail" :loading="loadingReenviarEmail">
            <v-icon class="mr-2">mdi-email-sync</v-icon>
            Enviar novamente
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import CreciHelper from '@/helpers/CreciHelper';
import StringHelper from '@/helpers/StringHelper';
import AppWebClient from '@/http/AppWebClient';
import SessionWebClient from '@/http/SessionWebClient';
import I2brWebClient from "@/http/I2brWebClient";
export default {
  name: 'Login',
  data: () => ({
    regionais: CreciHelper.regionais,
    showPassword: false,
    rules: {
      regionalRules: [
        v => !!v || 'Selecione o regional',
      ],
      cpfRules: [
        v => !!v || 'O CPF é obrigatório',
        v => StringHelper.validCpf(v) || 'Digite um CPF válido',
      ],
      passwordRules: [
        v => !!v || 'Digite sua senha',
        v => (!/\s/g.test(v)) || 'Espaços não são permitidos',
        v => (v && v.length >= 6) || 'Precisa ter pelo menos 6 caracteres',
        v => (v && v.length <= 16) || 'O limite é 16 caracteres',
      ],
      emailRules: [
        v => !!v || 'E-mail é obrigatório',
        v => /.+@.+\..+/.test(v) || 'Insira um E-mail válido',
      ]
    },
    iptRegional: null,
    iptCpf: '',
    iptPassword: '',
    iptPasswordRepeat: '',
    iptEmail: '',
    dialogRecover: false,
    dialogFirstAccess: false,
    dialogEmailAtivacao: false,
    disableForms: false,
    loadingLogin: false,
    loadingRecover: false,
    loadingFirstAccess: false,
    loadingReenviarEmail:false,
    emailAtivacao: '',
  }),
  computed: {
    compatibilidadeParcial() {
      if (!this.iptRegional) return false;
      const clienteI2br = I2brWebClient.regionais.includes(this.iptRegional);
      const clienteIncorpnet = this.iptRegional === 7 || this.iptRegional === 17;
      return !clienteI2br && !clienteIncorpnet;
    },
  },
  methods: {
    async submitLogin() {
      if (!this.$refs['form-login'].validate()) return;
      this.loadingLogin = true;
      this.disableForms = true;
      try {
        const sessionWebClient = new SessionWebClient();
        const token = await sessionWebClient.createSession(this.iptRegional, this.iptCpf, this.iptPassword);
        this.$store.commit('session/setToken', token);
        await this.$store.dispatch('ficha/reloadAll');
        await this.$store.dispatch('session/useIntervalValidator');
        await this.$router.push('/');
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
        this.$store.commit('session/setToken', null);
        if (!(e && e.response && e.response.data && e.response.data.mensagem)) {
          this.$alert({text: 'Falha no login', type: 'error'});
        }
        else if (e.response.status === 412) {
          this.emailAtivacao = e.response.data.dados;
          this.dialogEmailAtivacao = true;
        }
      } finally {
        this.loadingLogin = false;
        this.disableForms = false;
      }
    },
    async submitRecover() {
      if (!this.$refs['form-recover'].validate()) return;
      this.loadingRecover = true;
      this.disableForms = true;
      try {
        const appWebClient = new AppWebClient();
        const msg = await appWebClient.resetarSenha(this.iptCpf, this.iptRegional, this.iptEmail);
        if (msg) {
          this.$alert({text: msg, type: 'success', timeout: 3000});
          this.dialogRecover = false;
        }
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      } finally {
        this.loadingRecover = false;
        this.disableForms = false;
      }
    },
    async submitFirstAccess() {
      if (!this.$refs['form-firstaccess'].validate()) return;
      this.loadingFirstAccess = true;
      this.disableForms = true;
      try {
        const appWebClient = new AppWebClient();
        const msg = await appWebClient.insertUsuario(this.iptCpf, this.iptRegional, this.iptEmail, this.iptPassword);
        if (msg) {
          this.$alert({text: msg, type: 'success', timeout: 3000});
          this.dialogFirstAccess = false;
        }
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      } finally {
        this.loadingFirstAccess = false;
        this.disableForms = false;
      }
    },
    async reenviarEmail() {
      this.loadingReenviarEmail = true;
      try {
        const appWebClient = new AppWebClient();
        const msg = await appWebClient.reenviarConfirmacaoDeConta(this.iptCpf, this.iptRegional);
        if (msg) {
          this.$alert({text: msg, type: 'success', timeout: 3000});
          this.dialogEmailAtivacao = false;
        }
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      } finally {
        this.loadingReenviarEmail = false;
      }
    },
    rulePasswordRepeat(v) {
      return v === this.iptPassword || 'As senhas não estão iguais'
    },
  },
  watch: {
    dialogRecover(v) {
      if (v === false) {
        this.$refs['form-recover'].resetValidation();
      }
    },
    dialogFirstAccess(v) {
      if (v === false) {
        this.iptPassword = '';
        this.iptPasswordRepeat = '';
        this.$refs['form-firstaccess'].resetValidation();
      }
    }
  }
}
</script>

