import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import Login from '@/views/Login.vue'
import store from '@/store'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { hideNavbar: true },
  },
  {
    path: '/palavrapresidente/cofeci',
    name: 'Palavra-Presidente-Cofeci',
    component: () => import('../views/PalavraPresidenteCofeci'),
  },
  {
    path: '/palavrapresidente/creci',
    name: 'Palavra-Presidente-Creci',
    component: () => import('../views/PalavraPresidenteCreci'),
  },
  {
    path: '/config',
    name: 'Config',
    component: () => import('../views/Config'),
  },
  {
    path: '/carp',
    name: 'Carp',
    component: () => import('../views/Carp'),
  },
  {
    path: '/recad',
    name: 'Recad-Intro',
    component: () => import('../views/recad/Intro'),
  },
  {
    path: '/recad/historico',
    name: 'Recad-Historico',
    component: () => import('../views/recad/Historico'),
  },
  {
    path: '/recad/novo',
    name: 'Recad',
    component: () => import('../views/recad/Recadastramento'),
  },
  {
    path: '/notificacoes',
    name: 'Notificacoes',
    component: () => import('../views/Home'),
  },
  {
    path: '/contas',
    name: 'Contas',
    component: () => import('../views/Contas'),
  },
  {
    path: '/utilidades',
    name: 'Utilidades',
    component: () => import('../views/utilidades/MenuUtilidades'),
  },
  {
    path: '/utilidades/carp',
    name: 'Utilidades-Carp',
    component: () => import('../views/utilidades/CrecimsCarp'),
  },
  {
    path: '/perfil',
    name: 'Perfil',
    component: () => import('../views/Perfil'),
  },
];

const router = new VueRouter({routes});

router.beforeEach((to, from, next) => {
  if (store.getters['session/logged']) {
    if (to.name === 'Login') next('/');
    else next();
  } else {
    if (to.name === 'Login') next();
    else next({ name: 'Login' });
  }
});

export default router
