import Vue from 'vue'
import Vuex from 'vuex'
import { alertStore, sessionStore, snackbarStore, fichaStore } from './modules'
import vuexPersist from '@/plugins/vuex-persist'
import moment from 'moment';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dark: false,
    loader: false,
    showMenu: false,
    startTime: moment().format('YYYY-MM-DD HH:mm:ss'), //Hora que o app foi inicializado
    alertas: [],
  },
  mutations: {
    /**
     * Ativa ou desativa o tema escuro
     * @param state
     * @param {boolean} payload
     */
    setDark(state, payload) {
      state.dark = payload;
    },
    /**
     * Exibe ou esconde a tela de carregamento master da aplicação
     * @param state
     * @param {boolean} payload
     */
    setLoader(state, payload) {
      state.loader = payload;
    },
    /**
     * Exibe ou oculta o menu da aplicação
     * @param state
     * @param {boolean} payload
     */
    setMenu(state, payload) {
      state.showMenu = !!payload;
    },
    setStartTime(state) {
      state.startTime = moment().format('YYYY-MM-DD HH:mm:ss');
    },
    setAlertas(state, payload) {
      state.alertas = payload;
    },
  },
  actions: {
    async initializeApp({commit, dispatch}) {
      commit('setStartTime');
      const logged = await dispatch('session/validateSession');
      if (logged === true) {
        dispatch('ficha/reloadAll');
        dispatch('session/useIntervalValidator');
      }
    },
  },
  modules: {
    session: sessionStore,
    snackbar: snackbarStore,
    alert: alertStore,
    ficha: fichaStore,
  },
  plugins: [vuexPersist.plugin],
})
