<template>
  <v-app>
    <v-navigation-drawer
      app
      v-model="showMenu"
      v-if="$store.getters['session/logged'] && !$route.meta.hideNavbar && $store.state.ficha.cadastro" :dark="$store.state.dark"
    >
      <template v-slot:prepend>
        <v-list-item two-line :to="$store.state.ficha.cadastro && $store.state.ficha.cadastro.fonte === 'I2BR' ? '/perfil' : undefined">
          <v-list-item-avatar>
            <v-avatar>
              <img :src="$store.getters['ficha/avatar']" alt="" style="object-fit: cover">
            </v-avatar>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>{{nome}}</v-list-item-title>
            <v-list-item-subtitle>{{creci}}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </template>
      <v-divider></v-divider>
      <v-list dense nav>
        <v-list-item-group color="primary" v-if="limitarFuncoes">
          <v-list-item to="/">
            <v-list-item-icon>
              <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Inicio</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/notificacoes" disabled> <!-- TODO - Ativar notificações -->
            <v-list-item-icon>
              <v-icon>mdi-bell</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Notificações</v-list-item-title>
            </v-list-item-content>
            <v-list-item-action>
              <v-chip x-small color="error" label>EM BREVE</v-chip>
            </v-list-item-action>
          </v-list-item>
          <v-list-item to="/utilidades">
            <v-list-item-icon>
              <v-icon>mdi-palette-swatch</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Utilidades</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-divider></v-divider>
          <v-list-item to="/config">
            <v-list-item-icon>
              <v-icon>mdi-cog</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Configurações</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
        <v-list-item-group color="primary" v-else>
          <v-list-item to="/">
            <v-list-item-icon>
              <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Inicio</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/contas" v-if="$store.state.ficha.cadastro.contas.length > 1">
            <v-list-item-icon>
              <v-icon>mdi-account-switch</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Alternar conta</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/carp" :disabled="$store.state.ficha.cadastro.situacao !== 1">
            <v-list-item-icon>
              <v-icon>mdi-card-account-details</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Cartão de Regularidade</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/palavrapresidente/cofeci">
            <v-list-item-icon>
              <v-icon>mdi-account-tie</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Palavra Pres. COFECI</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/palavrapresidente/creci">
            <v-list-item-icon>
              <v-icon>mdi-account-tie-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Palavra Pres. CRECI</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item
            to="/recad"
            v-if="$store.state.ficha.cadastro && $store.state.ficha.cadastro.recad === true"
          >
            <v-list-item-icon>
              <v-icon>mdi-text-box-check</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Recadastramento</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item
            v-else-if="$store.state.ficha.cadastro && typeof $store.state.ficha.cadastro.recad === 'string'"
            :href="$store.state.ficha.cadastro.recad"
            target="_blank"
          >
            <v-list-item-icon>
              <v-icon>mdi-text-box-check</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Recadastramento</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item to="/notificacoes" disabled> <!-- TODO - Ativar notificações -->
            <v-list-item-icon>
              <v-icon>mdi-bell</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Notificações</v-list-item-title>
            </v-list-item-content>
            <v-list-item-action>
              <v-chip x-small color="error" label>EM BREVE</v-chip>
            </v-list-item-action>
          </v-list-item>
          <v-list-item to="/utilidades">
            <v-list-item-icon>
              <v-icon>mdi-palette-swatch</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Utilidades</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-divider></v-divider>
          <v-list-item to="/config">
            <v-list-item-icon>
              <v-icon>mdi-cog</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Configurações</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-main
      style="background-color: #f5f6f8"
      :class="{'white--text': $store.state.dark, 'black': $store.state.dark}"
    >
      <transition name="fade-transition" @after-leave="scrollTop" mode="out-in">
        <router-view/>
      </transition>
    </v-main>
    <v-footer
      app
      :color="$store.state.dark ? 'grey darken-4' : 'primary'"
      :dark="!$store.state.dark"
      class="py-1"
      :class="{'grey--text': $store.state.dark}"
    >
      <span v-if="$store.getters['session/logged'] && $route.name !== 'Login'">
        UID {{$store.getters['session/payload'].user}}
      </span>
      <v-spacer></v-spacer>
      v{{version}}
    </v-footer>
    <g-snackbar/>
    <g-alert/>
    <g-loader/>
  </v-app>
</template>

<script>
import GSnackbar from '@/components/global/GSnackbar';
import GAlert from '@/components/global/GAlert';
import GLoader from '@/components/global/GLoader';
import CreciHelper from '@/helpers/CreciHelper';
import JsonPackage from '../package.json';
import AppWebClient from '@/http/AppWebClient';
export default {
  name: 'App',
  components: {GLoader, GAlert, GSnackbar},
  data: () => ({
    showMenu: null,
    version: JsonPackage.version.substr(0, JsonPackage.version.indexOf('.', JsonPackage.version.indexOf('.') + 1)),
  }),
  methods: {
    scrollTop() {
      window.scrollTo(0, 0);
    },
    async loadAlertas() {
      const appWebClient = new AppWebClient(true);
      try {
        const alertas = await appWebClient.getAlertas();
        this.$store.commit('setAlertas', alertas);
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      }
    },
  },
  computed: {
    storeShowMenu() {
      return this.$store.state.showMenu;
    },
    nome() {
      return this.$store.state.ficha.cadastro ? this.$store.state.ficha.cadastro.nome : '...';
    },
    creci() {
      return this.$store.state.ficha.cadastro ? 'CRECI-' + CreciHelper.idParaUf(this.$store.getters['session/payload'].regional) + ' ' + this.$store.state.ficha.cadastro.creci : '...';
    },
    limitarFuncoes() {
      //Esta função é para ocultar funcionalidades do APP para o CPF
      if (this.$store.getters['session/payload'] && this.$store.getters['session/payload'].cpf) {
        return this.$store.getters['session/payload'].cpf === '04633822187';
      } else {
        return false;
      }
    },
  },
  watch: {
    storeShowMenu(v) {
      if (v !== this.showMenu) this.showMenu = v;
    },
    showMenu(v) {
      if (v !== this.$store.state.showMenu) this.$store.commit('setMenu', v);
    },
  },
  created() {
    this.loadAlertas();
  }
};
</script>
