import { httpSnackbar, httpSilent } from '@/plugins/axios'
import StringHelper from '@/helpers/StringHelper';

export default class AppWebClient {
  http = httpSnackbar();

  /**
   * Construtor
   * @param {boolean} silent
   */
  constructor(silent = false) {
    if (silent) this.http = httpSilent();
  }

  async getFicha() {
    const {data} = await this.http.get('ficha');
    return data;
  }

  async getFichaCompleta() {
    const {data} = await this.http.post('ficha');
    return data;
  }

  /**
   * Obtem a foto do corretor.
   * @returns {Promise<Blob>}
   */
  async getFoto() {
    const response = await this.http.get('usuario/foto', {responseType: 'blob', validateStatus: status => status === 200 || status === 404});
    if (response.status !== 200) return false;
    return response.data;
  }

  /**
   * Obtem a assinatura do corretor.
   * @returns {Promise<Blob>}
   */
  async getAssinatura() {
    const response = await this.http.get('usuario/assinatura', {responseType: 'blob', validateStatus: status => status === 200 || status === 404});
    if (response.status !== 200) return false;
    return response.data;
  }

  /**
   * Obtem os dados do presidente do CRECI ou COFECI, incluindo o texto de apresentação.
   * @param {number} regional
   * @returns {Promise<any>}
   */
  async getPresidente(regional) {
    const { data } = await this.http.get('presidente', {params: {'idregional': regional.toString()}});
    return data;
  }

  /**
   * Obtem a foto do presidente do CRECI ou COFECI.
   * @param {number} regional
   * @returns {Promise<boolean|Blob>} False caso nao tenha foto.
   */
  async getPresidenteFoto(regional) {
    const response = await this.http.get('presidente/foto', {params: {'id': regional.toString()}, responseType: 'blob',  validateStatus: status => status===200 || status===404});
    if (response.status !== 200) return false;
    return response.data;
  }

  /**
   * Obtem uma lista de informações dos recadastramentos realizados
   * @returns {Promise<any[]>}
   */
  async getRecadastramentos() {
    const {data} = await this.http.get('recadastramento');
    return data;
  }

  /**
   * Realiza o recadastramento.
   * @param {array<string>} emails
   * @param {array<{tipo: number, numero: string}>} telefones
   * @param {object} endereco
   * @param {string} pai
   * @param {string} mae
   * @param {number} estadoCivil
   * @param {number} formacao
   * @param {string} doador
   * @param {string} sexo
   * @param {Blob} foto
   * @param {Blob} assinatura
   * @param {object} enderecoComercial
   * @param {number} correspondencia 1 = Residencial, 2 = Comercial
   * @returns {Promise<void>}
   */
  async insertRecadastramento(emails, telefones, endereco, pai, mae, estadoCivil, formacao, doador, sexo, foto, assinatura, enderecoComercial = null, correspondencia = 1) {
    telefones = telefones.map(t => {
      //Higieniza o numero de telefone
      t.numero = t.numero.replace(/\D/g,'');
      return t;
    });
    const formData = new FormData();
    formData.append('dados', JSON.stringify({emails, telefones, endereco, pai, mae, estadoCivil, formacao, doador, sexo, enderecoComercial, correspondencia}));
    formData.append('foto', foto);
    formData.append('assinatura', assinatura);
    await this.http.post('recadastramento', formData);
  }

  /**
   * Obtem a imagem de frente do CARP. False em caso de erro.
   * @returns {Promise<boolean|Blob>}
   */
  async getCarpFrente() {
    const response = await this.http.get('carp/frente', {responseType: 'blob'});
    if (response.status !== 200) return false;
    return response.data;
  }

  /**
   * Obtem a imagem de frente do CARP. False em caso de erro.
   * @returns {Promise<boolean|Blob>}
   */
  async getCarpVerso() {
    const response = await this.http.get('carp/verso', {responseType: 'blob'});
    if (response.status !== 200) return false;
    return response.data;
  }

  /**
   * Obtem informacoes básicas do perfil do usuário, útil para montar um cartão de visita na tela.
   * @returns {Promise<any>}
   */
  async getPerfil() {
    const {data} = await this.http.get('perfil');
    return data;
  }

  //===== Conta de usuario =====

  /**
   * Cadastra uma nova conta de usuário.
   * @param {string} cpf
   * @param {number} regional Numero da regiao.
   * @param {string} email Email para envio da ativacao.
   * @param {string} senha Senha da conta.
   * @returns {Promise<boolean|string>} false = falha. string = mensagem de sucesso.
   */
  async insertUsuario(cpf, regional, email, senha) {
    const formData = new FormData();
    formData.append('cpf', StringHelper.extractNumbers(cpf));
    formData.append('regional', regional.toString());
    formData.append('email', email.trim());
    formData.append('senha', senha);
    const response = await this.http.post('usuario', formData);
    if (response.status !== 200) return false;
    return response.data;
  }

  /**
   * Altera a senha do usuario da sessão atual.
   * @param {string} senhaAtual
   * @param {string} senhaNova
   * @returns {Promise<void>}
   */
  async alterarSenha(senhaAtual, senhaNova) {
    const formData = new FormData();
    formData.append('current', senhaAtual.trim());
    formData.append('new', senhaNova.trim());
    await this.http.put('usuario', formData);
  }

  async resetarSenha(cpf, regional, email) {
    const formData = new FormData();
    formData.append('cpf', StringHelper.extractNumbers(cpf));
    formData.append('regional', regional.toString());
    formData.append('email', email.trim());
    const response = await this.http.patch('usuario', formData);
    if (response.status !== 200) return false;
    return response.data;
  }

  async reenviarConfirmacaoDeConta(cpf, regional) {
    const params = {'cpf' : StringHelper.extractNumbers(cpf), 'regional': regional.toString()}
    const response = await this.http.delete('usuario', {params});
    if (response.status !== 200) return false;
    return response.data;
  }

  //===== Termos de politica de privacidade =====//

  /**
   * Obtem uma listagem dos termos de Politica de Privacidade.
   * @returns {Promise<any[]>}
   */
  async getPoliticaTermos() {
    const params = {'listar': 'S'};
    const {data} = this.http.get('termo', {params});
    return data;
  }

  /**
   * Obtem o termo de Politica de Privacidade atual.
   * @returns {Promise<boolean|any>} False se não houver termo ativo.
   */
  async getPoliticaTermoAtual() {
    const response = await this.http.get('termo', {validateStatus: s => s === 200 || s === 404});
    if (response.status === 404) return false;
    return response.data;
  }

  /**
   * Obtem os dados de um termo.
   * @param {number} id Chave primaria do termo.
   * @returns {Promise<any>}
   */
  async getPoliticaTermo(id) {
    const params = {'id': id.toString()};
    const {data} = await this.http.get('termo', {params});
    return data;
  }

  /**
   * Lista as assinaturas/leituras confirmadas de um termo.
   * @param {number} termoId
   * @returns {Promise<any[]>}
   */
  async getPoliticaAssinaturas(termoId) {
    const params = {'termo': termoId.toString()};
    const {data} = await this.http.get('termo', {params});
    return data;
  }

  /**
   * Confere se o usuario assinou o termo de Politica de Privacidade.
   * @param {number|null} termoId Se nao especificar, sera checado o termo mais recente.
   * @returns {Promise<boolean>}
   */
  async getPoliticaAssinei(termoId = null) {
    const formData = new FormData();
    formData.append('conferir', 'S');
    if (termoId) formData.append('termo', termoId.toString());
    const {data} = await this.http.put('termo', formData);
    return data;
  }

  /**
   * Assina um termo, registrando sua leitura.
   * @param {number} termoId Se nao especificar, sera assinado o termo mais recente (nao recomendado).
   * @returns {Promise<void>}
   */
  async setPoliticaAssinatura(termoId) {
    const formData = new FormData();
    if (termoId) formData.append('termo', termoId.toString());
    const {data} = await this.http.put('termo', formData);
    return data;
  }

  //===== Alertas =====//

  /**
   * Lista os alertas.
   * @returns {Promise<any[]>}
   */
  async getAlertas() {
    const {data} = await this.http.get('alertas');
    return data;
  }
}