<template>
  <v-app-bar :color="$store.state.dark ? undefined : color" :dark="dark" :app="app" :flat="flat">
    <v-app-bar-nav-icon v-if="!hideNavBtn" @click="$store.commit('setMenu', !$store.state.showMenu)"></v-app-bar-nav-icon>
    <v-toolbar-title v-if="title">{{title}}</v-toolbar-title>
    <slot></slot>
    <v-spacer></v-spacer>
    <slot name="append"></slot>
  </v-app-bar>
</template>

<script>
  export default {
    name: 'AppBar',
    props: {
      title: {type: String, default: ''},
      color: {type: String, default: 'primary'},
      dark: {type: Boolean, default: true},
      app: {type: Boolean, default: true},
      flat: {type: Boolean, default: false},
      hideNavBtn: {type: Boolean, default: false},
    },
  }
</script>
