export default {
  namespaced: true,
  state: () => ({
    alerts: [], //Array<Alert>
  }),
  mutations: {
    /**
     * Exibe um alerta
     * @param state
     * @param {Alert} alert
     */
    show(state, alert) {
      if (state.alerts.find(a => a.text === alert.text)) return;
      const id = Date.now();
      state.alerts.unshift({id, ...alert});
      setTimeout(() => {
        const index = state.alerts.findIndex(a => a.id === id);
        if (index === -1) return;
        state.alerts.splice(index, 1);
      }, alert.timeout);
    },
  },
  // actions: {},
  // getters: {},
};
