import router from '@/router';
import JwtHelper from '@/helpers/JwtHelper';
import SessionWebClient from '@/http/SessionWebClient';

export default {
  namespaced: true,
  state: () => ({
    token: null,
    intervalId: null,
  }),
  mutations: {
    setToken(state, token) {
      state.token = token;
    },
    setIntervalId(state, intervalId) {
      state.intervalId = intervalId;
    },
  },
  actions: {
    /**
     * Destroi a sessão, retornando para a tela de Login.
     * @param commit
     * @returns {Promise<void>}
     */
    async destroy({ commit }) {
      commit('setToken', null);
      localStorage.clear();
      await router.push({name: 'Login'});
      commit('ficha/destroy', null, {root: true});
    },

    /**
     * Valida a sessão atual de login.
     * @param state
     * @param commit
     * @param dispatch
     * @param {boolean} exibirLoader Bloqueia a tela da aplicação, exibindo um overlay com loader.
     * @returns {Promise<boolean|null>} True = Logado e validado, False = Login inválido, Null = Não está logado.
     */
    async validateSession({state, commit, dispatch}, exibirLoader = true) {
      //TODO - Tirar a mensagem de SEM CONEXÃO quando estiver validando sem internet. Mas manter a mesagem de erro do servidor.
      let success = null;
      if (state.token) {
        if (exibirLoader) commit('setLoader', true, {root: true});
        const sessionWebClient = new SessionWebClient();
        try {
          await sessionWebClient.validateSession();
          success = true;
        } catch (e) {
          dispatch('destroy');
          success = false;
        } finally {
          if (exibirLoader) commit('setLoader', false, {root: true});
        }
      }
      if (state.intervalId !== null && !success) {
        window.clearInterval(state.intervalId);
        commit('setIntervalId', null);
      }
      return success;
    },

    /**
     * Fica validando periodicamente (a cada 60s) a sessão de login. Quando a validação der negativo, esta função se encerra.
     * @param dispatch
     * @param commit
     * @returns {Promise<void>}
     */
    async useIntervalValidator({dispatch, commit}) {
      const intervalId = window.setInterval(() => dispatch('validateSession', false), 60000);
      commit('setIntervalId', intervalId);
    },
  },
  getters: {
    logged(state) {
      return !!state.token;
    },
    /**
     * Obtem os dados do token da sessao
     * @param state
     * @returns {{cpf: string, date_login: string, regional: number, user: number}}
     */
    payload(state) {
      return state.token ? JwtHelper.getPayload(state.token) : null;
    },
  },
}