<template>
  <div>
    <app-bar title="iCorretor" flat>
      <template v-slot:append>
        <!-- Modo escuro rápido -->
        <v-btn
          icon
          v-if="$vuetify.breakpoint.smAndUp"
          @click="$store.commit('setDark', !$store.state.dark)"
        >
          <v-icon v-if="$store.state.dark">mdi-white-balance-sunny</v-icon>
          <v-icon v-else>mdi-weather-night</v-icon>
        </v-btn>
        <!-- TODO - Notificações -->
        <v-menu
          left
          bottom
          :dark="$store.state.dark"
          max-width="94%"
          offset-y
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-bell</v-icon>
            </v-btn>
          </template>
          <v-list
            two-line
            max-width="34rem"
            min-width="18rem"
            max-height="90vh"
            dense
            subheader
          >
            <v-subheader class="justify-center font-weight-bold">Notificações</v-subheader>
            <v-divider></v-divider>
            <p class="text-center mt-1 mb-0 grey--text">Em breve...</p>
          </v-list>
        </v-menu>
        <!-- Opções -->
        <v-menu
          left
          bottom
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-dots-vertical</v-icon>
            </v-btn>
          </template>
          <v-list dense class="py-0">
            <v-list-item to="/perfil" v-if="$store.state.ficha.cadastro && $store.state.ficha.cadastro.fonte === 'I2BR'">
              <v-list-item-icon>
                <v-icon>mdi-account</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Perfil comercial</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item to="/config">
              <v-list-item-icon>
                <v-icon>mdi-cog</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Configurações</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item @click="$store.dispatch('session/destroy')">
              <v-list-item-icon>
                <v-icon color="error">mdi-connection</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Desconectar</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-menu>
      </template>
    </app-bar>
    <home-status/>
    <v-alert
      v-for="i in $store.state.alertas.filter(i => i.local === 2)"
      :key="i.id"
      :type="i.tipo"
      class="mt-2 mb-0 mx-3 text-justify px-2"
      elevation="1"
      dense
      style="font-size: .9rem"
    >
      {{i.texto}}
    </v-alert>
    <home-info/>
  </div>
</template>

<script>
import AppBar from '@/components/AppBar';
import HomeStatus from '@/components/home/HomeStatus';
import HomeInfo from '@/components/home/HomeInfo';
import moment from 'moment';
export default {
  name: 'Home',
  components: {HomeInfo, HomeStatus, AppBar},
  created() {
    if (this.$store.state.ficha.cadastroData && this.$store.state.startTime) {
      const diffStart = moment().diff(this.$store.state.startTime, 'seconds');
      const diffUpdate = moment().diff(this.$store.state.ficha.cadastroData, 'seconds');
      if (diffUpdate >= 60 && diffStart >= 60) this.$store.dispatch('ficha/reloadCadastro');
    }
  },
}
</script>
