<template>
  <div
    class="d-flex flex-column justify-center align-center"
    style="height: 18rem; position: relative; overflow: hidden"
    :class="{'grey darken-4': $store.state.dark, 'primary': !$store.state.dark}"
  >
    <img :src="$store.getters['ficha/avatar']" alt="" class="avatar">
    <p class="mb-0 font-weight-bold white--text">SITUAÇÃO</p>
    <v-chip color="warning" label small v-if="$store.state.ficha.cadastro.situacao === 1 && !$store.state.ficha.cadastro.solenidade">AGUARDANDO SOLENIDADE</v-chip>
    <v-chip color="success" label small v-else-if="$store.state.ficha.cadastro.situacao === 1">ATIVO</v-chip>
    <v-chip color="error" label small v-else-if="$store.state.ficha.cadastro.situacao === 2 || $store.state.ficha.cadastro.situacao === 3 || $store.state.ficha.cadastro.situacao === 4 || $store.state.ficha.cadastro.situacao === 11">CANCELADO</v-chip>
    <v-chip color="error" label small v-else-if="$store.state.ficha.cadastro.situacao === 5 || $store.state.ficha.cadastro.situacao === 6 || $store.state.ficha.cadastro.situacao === 10">SUSPENSO</v-chip>
    <v-chip color="error" label small v-else>INATIVO</v-chip>
    <div style="height: 1.25rem; width: 100%" class="waves" :class="{dark: $store.state.dark}">
      <div class="wave"></div>
      <div class="wave2"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'HomeStatus'
  }
</script>

<style scoped>
  .avatar {
    height: 12rem;
    width: 9rem;
    object-fit: cover;
    background-color: white;
    border-radius: .5rem;
    box-shadow: .1rem .1rem .1rem rgba(0,0,0,.35);
    margin-bottom: .5rem;
  }
  .waves div {
    background-image: url('../../../src/assets/img/waves.png');
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    height: 101%;
    width: 200%;
    background-position: 0 bottom;
    background-repeat: repeat-x;
    transform-origin: center bottom;
  }
  .waves.dark div {
    background-image: url('../../../src/assets/img/waves_dark.png');
  }
  .wave {
    background-size: 50% .75rem;
    animation: waves 6s linear infinite;
  }
  .wave2 {
    background-size: 50% 1.25rem;
    animation: waves2 8s linear infinite;
    opacity: .35;
  }
  @keyframes waves {
    0% {
      transform: translateX(0) translateY(0) scaleY(1)
    }
    0% {
      transform: translateX(-25%) translateY(0) scaleY(.5)
    }
    0% {
      transform: translateX(-50%) translateY(0) scaleY(1)
    }
  }
  @keyframes waves2 {
    0% {
      transform: translateX(0) translateY(0) scaleY(1)
    }
    0% {
      transform: translateX(-25%) translateY(0) scaleY(.5)
    }
    0% {
      transform: translateX(-50%) translateY(0) scaleY(1)
    }
  }
</style>